import Link from 'next/link';
import Image from 'next/image';
import logoCeea from '../../public/logoceea.png';
import styles from '../ui/styles.module.css';
import ilustrationSchool1 from '../../public/school-ilustration.png';
import ilustrationSchool2 from '../../public/school-ilustration2.png';

export default function Page() {
  return (
    <main className="flex min-h-screen flex-col bg-yellow-500">
      <div className="flex h-[60px] shrink-0 justify-between items-center bg-yellow-500 p-4 md:h-[60px]">
        {/* azul: #010066 amarelo: #f5e600 */}
        <Image width={60}  src={logoCeea} alt="logo-ceea"/>
        <div className="flex justify-center items-center">
          <Link href="/" className="flex items-center gap-5 self-start rounded-lg  px-6 py-3 text-sm font-medium text-white ">
            <span >
              HOME
            </span>
          </Link>
          <Link href="/contato" className="flex items-center gap-5 self-start rounded-lg  px-6 py-3 text-sm font-medium text-white ">
            <span >
                CONTATO
            </span>
          </Link>
          <Link href="#" className="flex items-center gap-5 self-start rounded-lg  px-6 py-3 text-sm font-medium text-white ">
            <span >
                SOBRE
            </span>
          </Link>
        </div>
      </div>
      <div className={styles.background1}>
        <div className='container mx-auto'>
          <div className='flex flex-wrap'>
            <div className="w-full md:w-4/12 flex mt-20">
              <Image src={ilustrationSchool1} alt="illustration school" />
            </div>
            <div className="w-full md:w-8/12 mt-20 px-10">
              <h1 className='text-lg font-bold text-white text-center mt-10'>BEM VINDO AO CEEA</h1>
              <h1 className='text-center text-white font-bold mt-5'>Invista no futuro dos seus filhos! No Centro Educacional Evangélico Águia, Além de uma escola confessional com princípios e valores cristãos, oferecemos uma educação de excelência, unindo inovação e valores sólidos para formar cidadãos preparados para os desafios do mundo.</h1>
            </div>

          </div>
          <div className='flex flex-wrap justify-center'>
            <a href="#" className=" mt-20 inline-block px-6 py-2.5 bg-yellow-500 text-white font-medium text-xs leading-tight uppercase rounded shadow-md hover:bg-yellow-600 hover:shadow-lg focus:bg-yellow-600 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-yellow-700 active:shadow-lg transition duration-150 ease-in-out">
              CONTATO
            </a>

          </div>
        </div>
      </div>

      <div className={styles.background2}>
        <div className='container mx-auto'>
          <div className='flex flex-wrap'>
            
            <div className="w-full md:w-8/12 mt-20 px-10">
              <h1 className='text-lg font-bold text-white text-center mt-10'>A MELHOR DA REGIÃO!</h1>
              <h1 className='text-center text-white font-bold mt-5'> Com professores qualificados, infraestrutura moderna e projetos pedagógicos dinâmicos, nosso objetivo é desenvolver o potencial único de cada aluno, promovendo aprendizado significativo e realização pessoal. Venha fazer parte da nossa comunidade escolar e transforme hoje o amanhã do seu filho!</h1>
            </div>
            <div className="w-full md:w-4/12 flex mt-20">
              <Image src={ilustrationSchool2} alt="illustration school" />
            </div>
          </div>
          <div className='flex flex-wrap justify-center'>
            <a href="#" className=" mb-10 inline-block px-6 py-2.5 bg-yellow-500 text-white font-medium text-xs leading-tight uppercase rounded shadow-md hover:bg-yellow-600 hover:shadow-lg focus:bg-yellow-600 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-yellow-700 active:shadow-lg transition duration-150 ease-in-out">
              CONTATO
            </a>

          </div>
        </div>
      </div>

      <div className={styles.footer}>
        <h1 className='text-center text-white font-bold mt-5'>
          feito por: Barros Software
        </h1>
      </div>
    </main>
  );
}